import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import LoginForm from './components/LoginForm';
import RegisterForm from './components/RegisterForm';


class RLForm extends PureComponent {
  static propTypes = {
    type: PropTypes.string.isRequired,
    openLoginForm: PropTypes.func.isRequired,
    openRegisterForm: PropTypes.func.isRequired,
    close: PropTypes.func.isRequired,
  }

  constructor(props) {
    super(props);
    this.clickOutside = this.clickOutside.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  clickOutside(e) {
    const { target } = e;
    if (target.className.indexOf(`${this.props.type}Form`) !== -1) {
      this.props.close();
    }
  }

  closeModal() {
    this.props.close();
  }

  render() {
    return (
      <div
        className={`RLForm${(this.props.type ? ' modal-open' : '')}`}
        onClick={(e) => { this.clickOutside(e); }}
        role="presentation"
      >
        <RegisterForm
          active={this.props.type === 'register'}
          openLoginForm={this.props.openLoginForm}
          handleClose={this.closeModal}
        />
        <LoginForm
          active={this.props.type === 'login'}
          openRegisterForm={this.props.openRegisterForm}
          handleClose={this.closeModal}
        />
        <div className={`modal-backdrop fade${(this.props.type ? ' in' : '')}`} />
      </div>
    );
  }
}

export default RLForm;
